const showSkills = (triggerBtn, img, triggerName, score) => {
	const btn = document.querySelectorAll(triggerBtn);
	const pointer = document.querySelector(img);
	const nameSkill = document.querySelector(triggerName);
	const lvlScore = document.querySelector(score);

	let startScore = 0;
	let indexInterval = 0;
	let scoreValue;

	function clearRotate() {
		pointer.style.transform = '';
	}
	function toggleNameSkill(e) {
		nameSkill.style.opacity = '0';
		setTimeout(() => {
			nameSkill.style.opacity = '1';
			nameSkill.innerHTML = e.target.textContent;
		}, 400);
	}
	function changeColorScoreSkill() {
		if (scoreValue <= 400) {
			lvlScore.style.color = '#ffc815';
		} else if (scoreValue > 400 && scoreValue < 800) {
			lvlScore.style.color = '#a3cd3b';
		} else if (scoreValue > 800) {
			lvlScore.style.color = '#0099cc';
		}
	}
	function toggleScoreSkill(e) {
		scoreValue = e.target.id;
		changeColorScoreSkill();
		let animate;
		function animateMedia(dur, deg) {
			animate = gsap.to(pointer, {duration: dur,
				translateY: 40,
				rotation: deg});
		}
		// animate scale
		if (scoreValue <= 100) {
			animate = gsap.to(pointer, {duration: 1,
				rotation: 20});
		} else if (scoreValue > 100 && scoreValue <= 200) {
			animate = gsap.to(pointer, {duration: 1.5,
				rotation: 40});
		} else if (scoreValue > 200 && scoreValue <= 300) {
			animate = gsap.to(pointer, {duration: 1.7,
				rotation: 60});
		} else if (scoreValue > 300 && scoreValue <= 400) {
			animate = gsap.to(pointer, {duration: 2.5,
				rotation: 80});
		} else if (scoreValue > 400 && scoreValue <= 500) {
			animate = gsap.to(pointer, {duration: 2.7,
				rotation: 100});
		} else if (scoreValue > 500 && scoreValue <= 600) {
			if (window.matchMedia("(min-width: 1140px)").matches) {
				animateMedia(3.5, 120);
			} else {
				animate = gsap.to(pointer, {duration: 3.5,
					translateY: 15,
					rotation: 120});
			}
		} else if (scoreValue > 600 && scoreValue <= 700) {
			if (window.matchMedia("(min-width: 1140px)").matches) {
				animateMedia(3.7, 140);
			} else {
				animate = gsap.to(pointer, {duration: 3.7,
					translateY: 15,
					rotation: 140});
			}
		} else if (scoreValue > 700 && scoreValue <= 800) {
			if (window.matchMedia("(min-width: 1140px)").matches) {
				animateMedia(4, 160);
			} else {
				animate = gsap.to(pointer, {duration: 4,
					translateY: 15,
					rotation: 160});
			}
		} else if (scoreValue > 800 && scoreValue <= 900) {
			if (window.matchMedia("(min-width: 1140px)").matches) {
				animateMedia(4.5, 180);
			} else {
				animate = gsap.to(pointer, {duration: 4.5,
					translateY: 15,
					rotation: 180});
			}
		} else if (scoreValue > 900 && scoreValue <= 1000) {
			if (window.matchMedia("(min-width: 1140px)").matches) {
				animateMedia(4.7, 200);
			} else {
				animate = gsap.to(pointer, {duration: 4.7,
					translateY: 15,
					rotation: 200});
			}
		}
		function animateKill() {
			animate.kill();
		}
		// end animate scale
		const interval = setInterval(() => {
			lvlScore.innerHTML = startScore++;
			if (startScore > scoreValue || indexInterval > 1) {
				lvlScore.innerHTML = e.target.id;
				clearInterval(interval);
				animateKill();
				indexInterval--;
				clearRotate();
			}
		}, 6);
	}
	btn.forEach((item) => {
		item.addEventListener('click', (e) => {
			toggleNameSkill(e);
			toggleScoreSkill(e);
			clearRotate();
			++indexInterval;
			startScore = 0;
		});
	});
};

export default showSkills;
